/**
 *   Name: BizAgi Smartphone Render Cascading combo Extension
 *   Author: Oscar O
 *   Comments:
 *   -   This script will redefine the cascading combo render class to adjust to tablet devices
 */

// Extends from base cascading combo, then apply tablet combo stuff
bizagi.rendering.cascadingCombo.extend("bizagi.rendering.cascadingCombo", {}, {

    init: function(params){
        var self = this;
        self._super(params);
        this.properties.data = this.properties.data || [];

        self.bind("select", function (ex, value) {
            self.onComboChange(value);
        });
    },
    /*
    *
    * */
    renderReadOnly: function() {
        return bizagi.rendering.combo.prototype.renderReadOnly.apply(this, arguments);
    },

    /**
     * Initilizing the combo control
     * */
    renderSingle: function () {
        var self = this;
        self._super();

        var parentCombo = self.getParentCombo();
        
        if (parentCombo !== null && parentCombo.properties.type == "cascadingCombo") {
            self.isDependant = true;
            parentCombo.dependants.push(self);
            if (self.getValue() != null) {
                self.properties.parentComboValue = self.getValue().id;
            } else {
                self.initializingCascadingCombo = true;
            }
        }

        self.initialValueSet = true;
        $.when(bizagi.rendering.combo.prototype.renderSingle.apply(self, arguments))
            .done(function () {
                if (self.value && self.isDependant == false){
                    self.triggerHandler("select", self.value);
                }
                self.initialValueSet = false;
            });
    },

    /**
     * Postrender Method
     * */
    postRenderSingle: function () {
        bizagi.rendering.combo.prototype.postRenderSingle.apply(this, arguments);
    },

    renderEdition: function () {
        bizagi.rendering.combo.prototype.renderEdition.apply(this, arguments);
    },

    /**
     * Setting the combo's handlers
     * */
    configureCombo: function () {
        bizagi.rendering.combo.prototype.configureCombo.apply(this, arguments);
    },

    configureModalViewHandlers: function () {
        bizagi.rendering.combo.prototype.configureModalViewHandlers.apply(this, arguments);
    },

    filterList: function () {
        bizagi.rendering.combo.prototype.filterList.apply(this, arguments);
    },

    setModalViewDisplayValue: function () {
        bizagi.rendering.combo.prototype.setModalViewDisplayValue.apply(this, arguments);
    },

    /*
     *   Sets the value in the rendered control
     */
    setDisplayValue: function () {
        bizagi.rendering.combo.prototype.setDisplayValue.apply(this, arguments);
    },

    setValue: function (value, triggerEvents) {
        var self = this;
        triggerEvents = triggerEvents !== undefined ? triggerEvents : true;

        if (value === "") {
            value = { id: "", value: "" };
        }

        if (!!value) {
            if (self.value === null || self.value.id !== value.id){
                self.triggerHandler("select", value);
            }

            this._super(value, triggerEvents);
        }
    },

    /**
     * Finds an element in the combo data values by id
     * @param {int} id 
     * @returns {Object}
     */
    findDataValueById: function (id) {
        return (this.properties.data || []).find(function(e) {
            return e.id == id;
        });
    },

    /**
     * cleans the control's data
     * */
    cleanData: function() {
        bizagi.rendering.combo.prototype.cleanData.apply(this, arguments);
    },

    /**
     * cleans the control's data
     * */
    resetData: function() {
        bizagi.rendering.combo.prototype.resetData.apply(this, arguments);
    },

    processComboData: function(data){
        bizagi.rendering.combo.prototype.processComboData.apply(this, arguments);
    },

    validateValue: function(value){
        bizagi.rendering.combo.prototype.validateValue.apply(this, arguments);
    },

    /*
     *   Returns the selected value in the template
     */
    getSelectedValue: function () {
        return bizagi.rendering.combo.prototype.getSelectedValue.call(this, arguments);
    },

    /*
     *   Handler for combo change
     */
    onComboChange: function (newValue) {
        var self = this;
        self.selectedValue = newValue.value;
        var params = {};
        params[self.Class.BA_ACTION_PARAMETER_PREFIX + "parent"] = newValue.id;

        $.each(this.dependants, function(key, dependant){
            dependant.properties.params = params;
            dependant.properties.parentComboValue = newValue.id;

            if (newValue.id == ""){
                dependant.resetData();
                dependant.setDisplayValue({ id: "", value: "" });
            }else{
                $.when(dependant.getData(params)).done(function (data) {
                    dependant.properties.data = data;
                    if (dependant.value !== null){
                        var selected = dependant.findDataValueById(dependant.value.id);
                        if (selected){
                            if (selected.id == "" && (self.value && self.value.value == "")){
                                dependant.resetData();
                                dependant.setDisplayValue({ id: "", value: "" });
                            }else{
                                dependant.triggerHandler("select", selected);
                            }
                        }
                        else{
                            dependant.setDisplayValue({ id: "", value: "" });
                            dependant.triggerHandler("select", { id: "", value: "" });
                        }
                    }else{
                        dependant.triggerHandler("select", { id: "", value: "" });
                    }

                });
            }
        });
    },

    getTemplateName: function () {
        return "combo";
    }
});
