/*
*   Name: Bizagi tablet Date Column Decorator Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the date column decorator class to adjust to tablet devices
*/

// Extends itself
bizagi.rendering.columns.column.extend("bizagi.rendering.columns.date", {}, {
    applyOverrides: function(decorated) {
        var self = this;
        var mode = self.getMode();

        self._super(decorated);

        // Hacks the getControl method in the decorated render to add features
        if (decorated) {
            decorated.showTime = false;

            if (mode === "execution") {
                decorated.changeMinValue = function(value) {
                    // resolve array minvalue
                    var properties = this.properties;
                    var dateControl = this.getDateControl();
                    var calculateValue = self.resolveValue(this, value);

                    // Set value in control
                    if (value && properties.editable) {                        
                        if (typeof (calculateValue) === "number") {
                            calculateValue = new Date(calculateValue)                            
                            dateControl.mobiscroll('option', { min: calculateValue }); // DateTime Normal
                            calculateValue = bizagi.util.dateFormatter.formatInvariant(calculateValue, properties.showTime); // Set as invariant
                        } else {
                            dateControl.datepicker("option", "minDate", bizagi.util.dateFormatter.getDateFromInvariant(calculateValue)); // DateTime Normal
                        }

                        properties.minValue = value; // Invarian date   
                        decorated.properties.minValue = calculateValue;
                    }
                };

                decorated.changeMaxValue = function(value) {
                    // resolve array minvalue
                    var properties = this.properties;
                    var dateControl = this.getDateControl();
                    var calculateValue = self.resolveValue(this, value);

                    // Set value in control
                    if (value && properties.editable) {

                        if (typeof (calculateValue) === "number") {
                            calculateValue = new Date(calculateValue)
                            dateControl.mobiscroll('option', { max: calculateValue }); // DateTime Normal
                            calculateValue = bizagi.util.dateFormatter.formatInvariant(calculateValue, properties.showTime); // Set as invariant
                        } else {
                            dateControl.datepicker("option", "maxDate", bizagi.util.dateFormatter.getDateFromInvariant(calculateValue)); // DateTime Normal
                        }

                        properties.maxValue = value; // Invarian date       
                        decorated.properties.maxValue = calculateValue;
                    }
                };
            }
        }
    },

    resolveValue: function(context, value) {
        var result = "";

        if (bizagi.util.isString(value) || bizagi.util.isNumeric(value)) {
            result = value;
        }

        if (bizagi.util.isArray(value)) {
            var indexes = context.grid.getIndexes();
            var surrogateKey = context.surrogateKey;
            var index = indexes.indexOf(surrogateKey);
            if (index !== -1) {
                result = value[index];
            }
        }

        return result;
    }
});