/*
*   Name: BizAgi Tablet Number Column Decorator Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the number column decorator class to adjust to tablet devices
*/

// Extends from column
bizagi.rendering.columns.column.extend("bizagi.rendering.columns.number", {}, {
    
    applyOverrides: function (decorated) {
        this._super(decorated);
    }
});
