/**
 *   Name: Bizagi smartphone Render Upload Extension
 *   Author: Bizagi Mobile Team
 *   Comments:
 *   -   This script will redefine the upload render class to adjust to smartphones devices
 */

// Extends itself
bizagi.rendering.upload.extend("bizagi.rendering.upload", {
    BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
    BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
    QUALITY_PICTURE: 50,
    LIMIT: 1, // limit: The maximum number of multimedia files in the device user can record in a single capture operation.
    EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
    EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
    EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
    EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"]
}, {

    /**
     * Template method to implement in each children to customize each control
     * @returns {}
     */
    renderControl: function () {
        var self = this;
        var template = self.renderFactory.getTemplate("upload");
        var allow_upload = true;

        self.checkExtensions();

        if (bizagi.util.isCordovaSupported()) {
            allow_upload = self.validOptions && (self.validOptions.image || self.validOptions.video);
        }

        // Render template
        var html = $.kendoTmpl(template, {
            xpath: bizagi.util.encodeXpath(self.getUploadXpath()),
            editable: self.properties.editable && allow_upload,
            noFiles: (self.filesCount === 0),
            allow_upload: allow_upload,
            nonSupportedExtension: self.nonSupportedExtension,
            uploadOptions: self.upload_options,
            allowSendInMail: self.properties.allowSendInMail
        });

        // Render current children
        var items = "";
        for (var i = 0, filesCount = self.filesCount; i < filesCount; i++) {
            var file = { id: self.files[i][0], fileName: self.files[i][1] };
            var item = self.renderUploadItem(file);
            items += item;
        }

        html = self.replaceFilesHtml(html, items);
        return html;
    },

    /**
     * Renders a single upload item
     * @param {} file
     * @returns {}
     */
    renderUploadItem: function (file) {
        var self = this;
        var mode = self.getMode();
        var image = self.checkImageFile(file.fileName);
        var template = self.renderFactory.getTemplate("uploadItem");

        // Don't render urls when not running in execution mode
        var url = mode !== "execution" ? "javascript:void(0);" : self.buildItemUrl(file);

        var html = $.kendoTmpl(template, {
            url: url,
            image: image,
            allowDelete: self.properties.allowDelete,
            editable: self.properties.editable,
            filename: file.fileName,
            id: file.id,
            mode: mode,
            isCordovaSupported: bizagi.util.isCordovaSupported()
        });

        return html;
    },

    /**
     * Template method to implement in each device to customize each render after processed
     * @returns {}
     */
    postRenderSingle: function () {
        var self = this;
        var containerRender = self.getContainerRender();

        self.configureHelpText();

        self.itemAddfile = $(".bz-rn-upload-show-menu", self.getControl());

        if (!self.properties.editable || self.itIsMaxNumberFilesReached() || (!bizagi.util.isCordovaSupported() && !bizagi.util.media.fileAPISupported())) {
            self.itemAddfile.hide();
        }

        if (!self.properties.editable || (!bizagi.util.isCordovaSupported() && !bizagi.util.media.fileAPISupported())) {
            containerRender.addClass("bz-rn-non-editable");
        } else {
            containerRender.addClass("bz-command-edit-inline");
            self.buildUploadControl();
        }

        self.addInteractions();
    },

    buildUploadControl: function () {
        var self = this;
        var container = self.getControl();
        var fileInput = $(".bz-rn-upload-web", container);

        if (bizagi.util.isCordovaSupported()) {
            // Upload image
            $(self.itemAddfile).actionSheet({
                actions: self.upload_options,
                actionClicked: function (action) {
                    // Action for web file upload
                    if (action.guid === 5) {
                        fileInput.click();
                    } else {
                        self.handlerUploadImage(action.guid);
                    }
                },
                appear: function (action) {
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                cancelClicked: function (action) {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                }
            });

            // Resolution image
            $(".bz-rn-upload-resolution-container", container).actionSheet({
                actions: bizagi.util.media.getImageResolutionItems(),
                actionClicked: function (action) {
                    bizagi.loading.start();
                    $.when(self.handlerResolutionUpload(action.guid)).always(function () {
                        bizagi.loading.stop();
                    });
                },
                appear: function (action) {
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                cancelClicked: function (action) {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                }
            });
        } else {
            $(self.itemAddfile).click(function () {
                fileInput.click();
            });
        }

        fileInput.on("click", function (e) {
            e.stopPropagation();
        });

        // Attach event to web upload-control
        fileInput.on("change", function () {
            var that = this;
            var dataFile = that.files[0];
            var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSFILE, self.Class.EXTENSIONSAUDIO, self.Class.EXTENSIONSVIDEO);

            dataFile.fullPath = that.value;

            bizagi.loading.start();

            if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) && bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {

                $.when(self.processUploadFile(dataFile))
                    .then(function (result) {
                        self.onUploadFileCompleted(self, JSON.parse(decodeURIComponent(result)));
                    }).fail(function (error) {
                        if (error.statusText !== "Unauthorized" || error.statusText.toLowerCase() !== "ok") {
                            bizagi.showMessageBox(self.getResource("workportal-widget-admin-language-error"));
                        }
                    }).always(function () {
                        bizagi.loading.stop();
                    });
            } else {
                bizagi.loading.stop();
            }

            // Clearing input Values
            $(that).val("");
        });

        // Check files number
        self.checkMaxFiles();

        // Attach delete option
        self.attachDeleteHandler();
    },

    /**
     * Add the interactions to the upload control items
     * @return {}
     */
    addInteractions: function () {
        var self = this;

        // Handler to download the file
        $("ul.files", self.getControl()).on("click", ".ui-bizagi-render-link.button", function () {
            bizagi.loading.start();
            $.when(bizagi.util.media.downloadFile($(this).attr("data-url"), this.title)).fail(function (error) {
                self.executeFormFailHandler(error);
            }).always(function () {
                bizagi.loading.stop();
            });
        });
    },

    /**
     * Handler of delete action
     * @returns {}
     */
    attachDeleteHandler: function () {
        var self = this;

        $(".bz-rn-upload-delete-icon", self.getControl()).on("click", function () {
            var item = $(this).parent(".ui-bizagi-render-upload-item");
            var file = item.data();
            $.when(self.deleteUploadItem(item, file.id))
                .done(function () {
                    // Remove item
                    item.hide();
                    item.remove();

                    // Update value
                    self.setValue(self.files, false);

                    // Check maxFiles
                    self.checkMaxFiles();

                    // Trigger change
                    self.triggerRenderChange();
                });
        });
    },

    /**
     * Handler of upload image
     * @param {} ordinal
     * @returns {}
     */
    handlerUploadImage: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;

        switch (actionSelected) {
            case 2:
                navigator.camera.getPicture(function (dataImage) {
                    self.dataImage = dataImage;
                    var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSVIDEO);
                    bizagi.loading.start();

                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties))
                        .then(function () {
                            //Gets the real properties for the file
                            return $.when(bizagi.util.media.getFileDataInfo(self.dataImage))
                                .then(function (fileData) {
                                    self.fileProperties = fileData;
                                    if (bizagi.util.media.checkFileTypes(fileData, self.properties, extensions)) {
                                        if (!bizagi.util.isAndroid()) {
                                            return $.when(bizagi.util.media.modalResolutionUpload()).then(function (buttonIndex) {
                                                    if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                                                        return self.handlerResolutionUpload(buttonIndex);
                                                    }
                                                });

                                        } else {
                                            // force click to show actionsheet menu
                                            $(".bz-rn-upload-resolution-container", self.getContainerRender()).click();
                                        }
                                    }
                                });
                        }).always(function () {
                            bizagi.loading.stop();
                        });
                }, self.onFail.bind(self), {
                    quality: self.Class.QUALITY_PICTURE,
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY
                });
                break;
            case 1:
                navigator.camera.getPicture(function (dataImage) {
                    bizagi.loading.start();
                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties))
                        .then(function () {
                            return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (fileData) {
                                self.fileProperties = fileData;
                                return self.saveImage(self, dataImage);
                            });
                        }).always(function () {
                            bizagi.loading.stop();
                        });
                },
                    self.onFail.bind(self),
                    {
                        quality: self.Class.QUALITY_PICTURE,
                        sourceType: navigator.camera.PictureSourceType.CAMERA,
                        mediaType: navigator.camera.MediaType.PICTURE,
                        destinationType: Camera.DestinationType.FILE_URI,
                        correctOrientation: true,
                        targetWidth: 1280,
                        targetHeight: 960
                    });
                break;
            case 3:
                navigator.device.capture.captureVideo(function (dataVideo) {
                    bizagi.loading.start();

                    $.when(bizagi.util.media.checkMaxSizeVideo(dataVideo, self.properties))
                        .then(function () {
                            return self.saveVideo(self, dataVideo);
                        }).always(function () {
                            bizagi.loading.stop();
                        });
                }, self.onFail.bind(self), { limit: self.Class.LIMIT });
                break;
        }
    },

    /**
     * Handler of resolution upload
     * @param {} ordinal
     * @returns {}
     */
    handlerResolutionUpload: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;

        //if original, save image normally, if not, reduce quality and size
        if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
            return self.saveImage(self, self.dataImage);
        } else {
            var resolution = bizagi.util.media.getImageResolution(actionSelected);

            return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution))
                .then(function (result) {
                    self.editedImageURL = result.localUrl;
                    self.properties.url = result.base64Image;
                    return self.saveImage(self, self.dataImage);
                });
        }
    },

    /**
     * Call the form failhandler with an specific error
     * @param {} error
     * @returns {}
     */
    executeFormFailHandler: function (error) {
        var self = this;
        var errorResponse = error.responseText ? error.responseText : bizagi.localization.getResource("workportal-general-error"); self.getFormContainer().failHandler(errorResponse);
    },

    /**
     * CheckImageFile
     * @param {} fileName
     * @returns {}
     */
    checkImageFile: function (fileName) {
        var self = this;
        var validExtensions = self.Class.EXTENSIONSIMG;

        if (!fileName || bizagi.util.isObjectEmpty(fileName)) return false;

        for (var i = 0, length = validExtensions.length; i < length; i++) {
            var image = fileName.indexOf("." + validExtensions[i]);

            if (image >= 0) {
                return true;
            }
        }

        return false;
    },

    /**
     * Templates
     * @returns {}
     */
    getTemplateName: function () {
        return "upload";
    },

    getTemplateItemName: function () {
        return "uploadItem";
    },

    /**
     * Send image using HTTP
     * @param {} context
     * @param {} dataImage
     * @returns {}
     */
    saveImage: function (context, dataImage) {
        var self = context;
        var params = {};
        var addParams = self.buildAddParams();
        var fileName = self.fileProperties.name;

        if (self.editedImageURL) {
            params.dataFile = self.editedImageURL;
        } else if (self.fileProperties) {
            params.dataFile = self.fileProperties.url;
        }

        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());
        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileKey = "file";
        params.options.fileName = fileName;
        params.options.mimeType = "image/jpeg";
        params.options.uploadKey = { key: addParams["h_xpath"] };
        params.options.addUrl = self.properties.addUrl;

        //Cleans variables related previous uploads
        self.editedImageURL = null;
        self.fileProperties = null;

        // Fix android 4.4 getting images from recent folder
        params.dataFile = bizagi.util.media.getImagePath(params.dataFile);

        return $.when(bizagi.util.media.uploadFile(params)).then(function (r) {
            var response = JSON.parse(decodeURIComponent(r.response));
            if (response.type === "error") {
                bizagi.showMessageBox(response.message, response.errorType);
            } else {
                self.onUploadFileCompleted(context, response);
            }
        }).fail(function (error) {
            self.executeFormFailHandler(error);
        });
    },

    /**
     * saveVideo
     * @param {} context
     * @param {} dataVideo
     * @returns {}
     */
    saveVideo: function (context, dataVideo) {
        var self = context;
        var params = {};
        var addParams = self.buildAddParams();

        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());

        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileName = dataVideo[0].name;
        params.options.mimeType = "video/quicktime";
        params.options.uploadKey = { key: addParams["h_xpath"] };
        params.options.addUrl = self.properties.addUrl;
        params.dataFile = dataVideo[0].fullPath;

        return $.when(bizagi.util.media.uploadFile(params)).then(function (r) {
            var response = JSON.parse(decodeURIComponent(r.response));
            if (response.type === "error") {
                bizagi.showMessageBox(response.message, response.errorType);
            } else {
                self.onUploadFileCompleted(context, response);
            }
        }).fail(function (error) {
            self.executeFormFailHandler(error);
        })
    },

    /**
     * Management fail messages
     */
    onFail: function (error) {
        var messageError = bizagi.util.processFailMessage(error);

        bizagi.loading.stop();

        if (typeof (messageError) == "string" && messageError.indexOf("No Activity found to handle Intent") !== -1) {
            var hasResource = bizagi.util.isValidResource("render-upload-error-media-app");
            var message = hasResource ? bizagi.localization.getResource("render-upload-error-media-app") : "Your device does not have an app to execute this action.";

            bizagi.showMessageBox(message);
        }
    },

    /**
     * onUploadFileCompleted
     * @param {} context
     * @param {} response
     * @returns {}
     */
    onUploadFileCompleted: function (context, result) {
        var self = context;
        var control = self.getControl();
        var uploadWrapper = $(".ui-bizagi-render-upload-container .files", control);

        if (result.id && result.fileName) {
            // Locate it before the upload wrapper
            var newItem = self.renderUploadItem(result);
            uploadWrapper.append(newItem);

            // Increment counter
            self.filesCount = self.filesCount + 1;

            // Copy the files value but not its reference
            var files = self.files.slice();

            //Add the new item to this temporal array
            files.push([result.id, result.fileName]);

            // Update value
            self.setValue(files, true);

            // Check maxFiles
            self.checkMaxFiles();

            if (self.properties.allowDelete && self.properties.editable) {
                self.attachDeleteHandler();
            }

            self.editedImageURL = null;
        } else {
            bizagi.showMessageBox(result.message, "Error");
        }
    },

    /**
    * Checks if the control data is valid
    * @returns Boolean
    */
    isValid: function (invalidElements) {
        var self = this;
        //Call Base
        var bValid = self._super(invalidElements);

        if (bValid) {
            // Clear error message
            self.setValidationMessage("");
        }

        return bValid;
    },

    /**
     * Check maximum number of files
     * @returns {}
     */
    checkMaxFiles: function () {
        var self = this;
        var properties = self.properties;
        var control = self.getControl();
        var maxFiles = properties.maxfiles;

        var actualFiles = properties.value ? properties.value.length : 0;

        if (properties.editable) {
            if (maxFiles !== 0 && ((self.filesCount >= maxFiles && actualFiles >= maxFiles))) {
                self.itemAddfile.hide();
            } else {
                self.itemAddfile.show();
            }
        } else {
            //Checks if the files count is equals to 0, to show the no files dialog
            if (self.filesCount == 0) {
                $("#no-files", control).show();
            }
        }
    },

    /**
     * Check allow extensions and define possible choices
     * @returns {}
     */
    checkExtensions: function () {
        var self = this;
        var validExtensions = self.properties.validExtensions || "";

        self.validOptions = { audio: false, video: false, image: false, file: false };

        // Preprocess the extensions string
        validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();

        //Helps to get the non-supported extensions for mobile
        var nonSupportedExtensionsArr = [];

        // Initizalice the nonSupportedExtension element
        self.nonSupportedExtension = "";

        if (typeof (validExtensions) === "undefined" || validExtensions === "") {
            self.validOptions = {
                video: true,
                image: true,
                file: true
            };
        } else {
            validExtensions = validExtensions.split(";");

            if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) {
                self.validOptions.video = true;
                self.validOptions.image = true;
            } else {
                for (var i = 0, length = validExtensions.length; i < length; i++) {
                    validExtensions[i] = validExtensions[i].replace("*.", "");

                    if (self.Class.EXTENSIONSFILE.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.file = true;
                    } else if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.image = true;
                    } else if (self.Class.EXTENSIONSVIDEO.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.video = true;
                    } else {
                        //If the extension is not part or any category, it is stored in nonSupportedExtensions array
                        nonSupportedExtensionsArr.push(validExtensions[i]);
                    }
                }
            }
        }

        if (!self.validOptions.file && !self.validOptions.image && !self.validOptions.video) {
            if (typeof (self.itemAddfile) !== "undefined") {
                self.itemAddfile.hide();
            }
        }

        // Get upload options
        self.getUploadChoices();

        // Adjust file extension, in order to make it multiline & Stores the non-supported extensions
        self.nonSupportedExtension = nonSupportedExtensionsArr.join(", ") || "";
    },

    /**
     * Check if the maximun number of files than can be uploaded has been reached
     * @returns {}
     */
    itIsMaxNumberFilesReached: function () {
        var maxFiles = this.properties.maxfiles;
        var actualFiles = this.properties.value ? this.properties.value.length : 0;

        return maxFiles !== 0 && (actualFiles >= maxFiles);
    },

    /**
     * Build add params to send to the server
     * @param {} dataFile
     * @returns {}
     */
    buildAddFileParams: function (dataFile) {
        var self = this;
        var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;
        var formData = new FormData();
        var form = self.getFormContainer();

        // Build add params to send to the server
        formData.append(prefix + "xpath", self.getUploadXpath());
        formData.append(prefix + "idRender", self.properties.id);
        formData.append(prefix + "xpathContext", self.properties.xpathContext || this.getXpathContext());
        formData.append(prefix + self.Class.BA_PAGE_CACHE, self.properties.idPageCache);
        formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
        formData.append(self.properties.xpath, dataFile);

        if (self.properties.contexttype) {
            formData.append(prefix + "contexttype", self.properties.contexttype);
        }

        try {
            if (typeof (BIZAGI_SESSION_NAME) !== "undefined") {
                formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
            } else {
                formData.append("JSESSIONID", form.properties.sessionId);
            }
        } catch (e) {
            formData.append("JSESSIONID", form.properties.sessionId);
        }

        return formData;
    },

    /**
     * Get the XpathContext in a grid
     */
    getXpathContext: function() {
        var self = this;
        if(self.grid) {
            var surrogateKey = this.surrogateKey || "";
            var self = this;
            return self.grid.properties.xpath + "[id=" + self.surrogateKey + "]";
        } else {
            return "";
        }
    },

    /**
     * Process an upload file into the server
     * @param {} context
     * @param {} dataFile
     * @returns {}
     */
    processUploadFile: function (dataFile) {
        var data = { url: this.properties.addUrl, formData: this.buildAddFileParams(dataFile) };
        return this.dataService.processUploadFile(data);
    },

    clearDisplayValue: function () {
        var self = this;
        var items = $(".ui-bizagi-render-upload-item", self.getControl());

        items.each(function (index, item) {
            item = $(item);
            var file = item.data();

            $.when(self.deleteUploadItem(item, file.id)).done(function () {
                // Remove item
                item.hide();
                item.remove();

                // Update value
                self.setValue(self.files, false);

                // Check maxFiles
                self.checkMaxFiles();

                // Trigger change
                self.triggerRenderChange();
            });
        })
    },

    getUploadChoices: function () {
        var self = this;
        var itemsUpload = [];

        // Add upload options
        if (self.validOptions.file && bizagi.util.media.fileAPISupported() && (bizagi.util.isAndroidDevice() && !bizagi.util.isCordovaSupported())) {
            // Upload any file
            itemsUpload.push({ "guid": 5, "displayName": bizagi.localization.getResource("render-upload-link-label") });
        }

        if (bizagi.util.isCordovaSupported()) {
            if (self.validOptions.image) {
                // Take photo, Choose photo
                itemsUpload.push({
                    "guid": 1,
                    "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-photo")
                });
                itemsUpload.push({
                    "guid": 2,
                    "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-choose-photo")
                });
            }

            if (self.validOptions.video) {
                // Take video
                itemsUpload.push({
                    "guid": 3,
                    "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-video")
                });
            }
        }

        // Stores the upload options
        self.upload_options = [].concat(itemsUpload);
    }

});